html {
  box-sizing: border-box;
  background: rgb(248, 221, 225);
}

img {
  border-radius: 20px;
}

img:hover {
  scale: 1.02;
  opacity: 0.9;
  cursor: pointer;

}

a {
  text-decoration: none;
}

.science-cards {
  display: flex;
  flex-direction: column;
  width: 50vw;
  margin: 2rem auto;
  border-radius: 20px;
  padding: 10px;
  background: rgb(241, 183, 38);
  padding: 20px;
  box-shadow: rgba(62, 59, 59, 0.3) 5px 6px 2px;
}

@media screen and (min-width: 900px) {
  .science-cards {
    width: 25vw;
    margin: 2.5rem auto;
    justify-content: space-around;
  }

  div {
    display: flex;
    flex-direction: row;
    width: 100vw;
  }
}